import { SystemCssProperties } from "@mui/system"
import { Box, Skeleton, SxProps } from "@rp/react/web/mui/core"
import { AdminTable } from "./AdminTable"
import { AdminTableRow } from "./AdminTableRow"

type Props = {
  rows: number
  labels: Array<{ text: string; sx?: SxProps }>
  sx?: SystemCssProperties
}

const LoadingAdminTableRow = ({ index }: { index: number }) => {
  return (
    <AdminTableRow index={index}>
      <Box sx={{ width: 50 }}>
        <Skeleton width={40} height={40} variant="rounded" />
      </Box>
      <Box sx={{ flexGrow: 1, pl: 1 }}>
        <Skeleton width={150} />
        <Skeleton width={250} />
      </Box>
      <Box sx={{ width: 250 }}>
        <Skeleton width={150} />
      </Box>
      <Box sx={{ width: 250 }}>
        <Skeleton width={150} />
      </Box>
      <Box sx={{ width: 50, display: "flex", justifyContent: "center" }}>
        <Skeleton width={50} />
      </Box>
    </AdminTableRow>
  )
}

const LoadingAdminTable = ({ rows, labels, sx }: Props) => {
  return (
    <AdminTable
      data={new Array(rows).fill("")}
      renderRow={(_, index) => <LoadingAdminTableRow key={index} index={index} />}
      labels={labels}
      sx={sx}
    />
  )
}
export { LoadingAdminTable }
